// Functions
export function isCircle(shape: SceneNode): shape is EllipseNode {
  return shape.type === "ELLIPSE" && shape.width === shape.height
}

export function isSquare(shape: SceneNode): shape is RectangleNode {
  return shape.type === "RECTANGLE" && shape.width === shape.height
}

export function createNewRectangle({x, y, width, height, fills, parent}): RectangleNode {
  const rect = figma.createRectangle()
  rect.x = x
  rect.y = y
  rect.resize(width, height)
  rect.fills = fills
  parent.appendChild(rect)
  return rect
}